<template>
  <div class="home">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">主页</el-menu-item>
        <el-submenu index="2">
            <template slot="title">分享</template>
            <el-submenu index="2-1">
              <template slot="title">生活</template>
              <el-menu-item index="2-1-1">周一工作</el-menu-item>
              <el-menu-item index="2-1-2">周二摸鱼</el-menu-item>
              <el-menu-item index="2-1-3">周三影院</el-menu-item>
              <el-menu-item index="2-1-4">周四读书</el-menu-item>
              <el-menu-item index="2-1-5">周五美食</el-menu-item>
              <el-menu-item index="2-1-6">周六风景</el-menu-item>
              <el-menu-item index="2-1-7">周日教堂</el-menu-item>
            </el-submenu>
            <el-menu-item index="2-2">健康</el-menu-item>
            <el-menu-item index="2-3">工作</el-menu-item>
            <el-submenu index="2-4">
              <template slot="title">技术</template>
              <el-menu-item index="2-4-1">前端</el-menu-item>
              <el-menu-item index="2-4-2">后端</el-menu-item>
              <el-menu-item index="2-4-3">网络</el-menu-item>
            </el-submenu>
        </el-submenu>
        <el-menu-item index="3" disabled>资源</el-menu-item>
        <el-menu-item index="4"><a href="home" target="_blank">四海</a></el-menu-item>
    </el-menu>

    <el-row id="sayPage" type="flex" justify="space-around">
      <el-col id="side" :span="6" class="hidden-sm-and-down">
        <div class="item">
          <UserSide />
        </div>
        <div class="item">
          <tag />
        </div>
      </el-col>
      <el-col :span="16" class="say-right-side">
        <el-row class="say-item">
            <el-card class="box-card" shadow="always">
              <span></span>
              <el-input class="input-say"
                  type="textarea"
                  placeholder="今天天气很好，有什么想说的吗?"
                  :autosize="{ minRows: 6, maxRows: 14}"
                  v-model="say.content"
                  show-word-limit>
              </el-input>
              <span></span>
              <el-button class="say-send-btn" type="primary" size="small" @click="saveSay">说完了</el-button>
            </el-card>
        </el-row>

        <el-col class="art-item">
          <el-card class="box-card" v-for="(sayItem, index) in sayList" :key="index">
            <div>
              <span>{{sayItem.content}}</span>
            </div>
          </el-card>
        </el-col>
        
        <div class="block pagination">
          <el-pagination background="#f9f9f9" layout="prev, pager, next" :total="50" />
        </div>
      </el-col>
    </el-row>

  </div>
</template>
<script>
import UserSide from '../components/UserSide'
import tag from '../components/tag'
import {postRequest} from '../utils/api'
import {getRequest} from '../utils/api'

export default {
  name: 'Home2',
  components: {
    UserSide,
    tag
  },
  data() {
    return {
      sayList: [],
      queryParam: {
        pagesize: 10,
        pagenum: 1
      },
      currentPage: 1,
      count: 0,
      total: 0,
      say: {
          id: '5',
          content: ''
      }
    }
  },
  mounted() {
    this.getSayList()
  },
  methods: {
    handleCurrentChange: function(currentPage) {
      this.queryParam.pagenum = currentPage
      // eslint-disable-next-line no-console
      console.log(this.currentPage) // 点击第几页
      this.getSayList()
    },
    // 获取文章列表
    getSayList() {
      var _this = this
      console.log("getting articles")
      getRequest('/say/list').then(resp => {
        if (resp.status === 200) {
          var json = resp.data 
          _this.sayList = json.says;
          console.log("backend sent saylist json：", json)
        } else {
          console.log("backend bad response")
        }
      })
    },
    saveSay() {
        var _this = this
        this.loading = true
        let param = this.say 
        postRequest('/say/add', {
            content: this.say.content
        }).then(resp => {
            _this.loading = false
            if (resp.status === 200) {
              this.say.content = ''
                var json = resp.data
                console.log('backend sent json: ', json)
                if (json.status === 'success') {
                    console.log("we are success")
                    this.getSayList()
                } else {
                    _this.$alert('status is not success', 'feailed!')
                }
            } else {
                _this.$alert('post say is failed', 'failure')
            }
        }, resp => {
            _this.loading = false 
            _this.$alert('cant not connect the host', 'faiiiled')
        })
    }
  }
}
</script>

<style scoped>
#side .item {
  margin-bottom: 30px;
}

.art-item {
  margin-bottom: 30px;
  position: relative;
}
.say-item {
  margin-bottom: 50px;
  position: relative;

}
.say-item span {
  display: inline-block;
  min-width: 484px;
  height: 2px;
  padding: 0 2px;
}

.art-item .star {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 0;
  right: 0;
}

img.tag {
  width: 16px;
  height: 16px;
}

.art-title {
  border-left: 3px solid #F56C6C;
  padding-left: 5px;
  cursor: pointer;
}

.art-title:hover {
  padding-left: 10px;
  color: #409EFF;
}

.art-time {
  margin-right: 20px;
}

.art-body {
  display: flex;
  padding: 10px 0;
}

.side-img {
  height: 150px;
  width: 270px;
  overflow: hidden;
  margin-right: 10px;
}

img.art-banner {
  width: 100%;
  height: 100%;
  transition: all 0.6s;
}

img.art-banner:hover {
  transform: scale(1.4);
}

.side-abstract {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.art-abstract {
  flex: 1;
  color: #aaa;
}

.art-more {
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.art-more .view {
  color: #aaa;
}
h5{
  font-size: 18px;
}
.pagination {
  background-color: #F9F9F9;
}
.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.box-card {
  display: flex;
  padding: 10px 0;
}
.input-say {
  display: inline-block;
  width: 100%;
  padding-bottom: 20px;
}
.say-send-btn {
  float: right;
}
.say-right-side {
  padding-top: 20px;
}
</style>



